// Create gallery markup
const gallery = document.querySelector(".gallery");

const galleryMarkup = images.map(({ preview, original, description }) => 
`<li class="gallery-item">
  <a class="gallery-link" href="${original}">
    <img
      class="gallery-image"
      src="${preview}"
      alt="${description}"
    />
  </a>
</li>`).join("")

gallery.insertAdjacentHTML("afterbegin", galleryMarkup)

// Icons for navigation lightbox
const iconNavLeft = new URL('../img/icons.svg#icon-nav-left', import.meta.url).href;
const iconNavRight = new URL('../img/icons.svg#icon-nav-right', import.meta.url).href;
const iconClose = new URL('../img/icons.svg#icon-close', import.meta.url).href;

// Initialising library SimpleLightbox
const lightbox = new SimpleLightbox('.gallery a', {
    captionsData: 'alt',
    captionDelay: 250,
    navText: [`<svg width="24" height="24"><use href="${iconNavLeft}"></use></svg>`,
        `<svg width="24" height="24"><use href="${iconNavRight}"></use></svg>`],
    closeText: `<svg width="20" height="20"><use href="${iconClose}"></use></svg>`
})

